<template>
  <div>
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="文章标题" prop="artTitle">
                        <el-input type="text" v-model="ruleForm.artTitle" ></el-input>
                    </el-form-item>
                    <el-form-item label="文章标签" prop="artLabel">
                        <el-input type="text" v-model="ruleForm.artLabel" ></el-input>
                    </el-form-item>
                    <el-form-item label="文章内容" prop="artNeirong">
                        <el-input v-model="ruleForm.artNeirong"></el-input>
                    </el-form-item>
                    <el-form-item label="是否推荐" >
                        
                        <el-switch
                        v-model="isRecom"
                        active-color="#13ce66"
                        inactive-color="#ff4949">
                        </el-switch>
                    </el-form-item>
                    <el-form-item label="上传文章" >
                        
                        <el-upload
                            class="upload-demo"
                            drag
                            action="/node/admin/uploadArt"
                            ref="uploads"
                            :on-success="uploadSuccess"
                            :on-remove="removeMd"
                            :on-change="onChange"
                            :limit="1"
                            :auto-upload="false"
                            >
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            
                        </el-upload>

                    </el-form-item>
                    
                    <el-form-item>
                        <el-button type="primary" @click="submitArt">上传文章</el-button>
                        <el-button @click="resetArt('ruleForm')">重置</el-button>
                    </el-form-item>
                </el-form>
  </div>
</template>

<script>
export default {
    name:'artiUpload',
    data(){
        var artNeirong = (rule, value, callback) => {
            if (!value) {
            return callback(new Error('内容不能为空'));
            }
        };
        var validateartTitle = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('文章标题不能为空'));
            } else {
            
            callback();
            }
        };
        var validateartLabel = (rule, value, callback) => {
            if (value === '') {
            callback(new Error('文章标签不能为空'));
            } else {
            callback();
            }
        };
        return{
            //是否推荐
            isRecom:false,
            //文件存储位置保存
            mdUrl:'',
            //表单数据绑定
            ruleForm: {
            artTitle: '',
            artLabel: '',
            artNeirong: ''
        },
        //表单规则
        rules: {
          artTitle: [
            { validator: validateartTitle, trigger: 'blur' }
          ],
          artLabel: [
            { validator: validateartLabel, trigger: 'blur' }
          ],
          artNeirong: [
            { validator: artNeirong, trigger: 'blur' }
          ]
        }
        }
    },
    methods: {
        uploadSuccess(res,file,filelist){
            // console.log(res);
            this.mdUrl = res.data
        },
        async removeMd(file,filelist){
            let {data} = await this.$axios.post('/node/admin/deleteMd',{
                path:this.mdUrl
            })
            console.log(data);
        },
        async submitArt() {
            if(!this.ruleForm.artTitle || !this.ruleForm.artLabel || !this.ruleForm.artNeirong) return this.$message.error('输入完整')
              let {data} = await this.$axios.post('/node/admin/submitArt',{
                  url:this.mdUrl,
                  artTitle:this.ruleForm.artTitle,
                  artLabel:this.ruleForm.artLabel,
                  artNeirong:this.ruleForm.artNeirong,
                  isRecom:this.isRecom
              })
            //   console.log(data);
            this.$message.success(data)
              this.submitAfter()

        },
        onChange(){
            this.$refs.uploads.submit()
        },
        resetArt(formName) {
            this.$refs[formName].resetFields();
        },
        submitAfter(){
            this.mdUrl =''
            this.ruleForm.artTitle=''
            this.ruleForm.artLabel=''
            this.ruleForm.artNeirong=''
        }
        
    },
    beforeDestroy(){
        if(this.mdUrl){
            this.removeMd()
        }
        this.submitAfter()

    },
}
</script>

